import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { Card, Row, message, Col } from 'antd';
import routeConstant from 'routes/route/routeConstant';
import { Link } from 'react-router-dom';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { getListPrizeInTrainType, deletePrizeInTrainType } from 'services/PrizeInTrainType';

class StipulateAmountOfScholarshipPage extends Component {
  state = { reload: 0 };
  handleDelete = async record => {
    await deletePrizeInTrainType(record);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const { reload } = this.state;

    const columns = [
      {
        title: 'Mã xếp loại',
        dataIndex: 'prizeClassId',
        key: 'prizeClassId',
      },
      {
        title: 'Xếp loại',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Hệ',
        dataIndex: 'trainTypeLabel',
        key: 'trainTypeLabel',
      },
      {
        title: 'Từ điểm HT 10',
        dataIndex: 'gradeIn10',
        key: 'gradeIn10',
      },
      {
        title: 'Từ điểm HT 4',
        dataIndex: 'gradeIn4',
        key: 'gradeIn4',
      },
      {
        title: 'Từ điểm RL',
        dataIndex: 'gradeInPractice',
        key: 'gradeInPractice',
      },
      {
        title: 'Số tiền',
        dataIndex: 'amount',
        key: 'amount',
      },
      {
        title: 'Khối ngành',
        dataIndex: 'studyFieldGroupLabel',
        key: 'studyFieldGroupId',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: routeConstant.HOC_BONG__QUYET_DINH_SO_TIEN__NHAP_DU_LIEU,
                search: `?id=${record.prizeClassId}&code=${record.trainTypeId}`,
              },
            }}
            onConfirmDelete={() => this.handleDelete(record)}
          />
        ),
      },
    ];
    return (
      <Card title="Quy định xếp loại học bổng">
        <Row>
          <Col span={24}>
            <CartHeadAction>
              <Link to={routeConstant.HOC_BONG__QUYET_DINH_SO_TIEN__NHAP_DU_LIEU}>
                <BtnAddNew />
              </Link>
            </CartHeadAction>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              service={getListPrizeInTrainType}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <StipulateAmountOfScholarshipPage />
  </Page>
);



